.sidebar-title {
  font-size: 22px;
  font-weight: normal;
  margin-bottom: 5px;
  /* margin-bottom: 20px; */
  margin-top: 15px;
}

.sidebar-label-container {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 8px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  font-size: 13px;
}
.sidebar-label-container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}
.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 14px;
  width: 14px;
  background-color: #eee;
  border-radius: 50%;
}
.sidebar-label-container:hover input ~ .checkmark {
  background-color: #ccc;
}
.sidebar-label-container input:checked ~ .checkmark {
  background-color: #2196f3;
}
.checkmark::after {
  content: "";
  position: absolute;
  display: none;
}
.sidebar-label-container input:checked ~ .checkmark::after {
  display: block;
}
.sidebar-label-container .checkmark::after {
  top: 4px;
  left: 4px;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: white;
}
.line {
  margin-top: 3rem;
  border-color: #f7f7f7;
}
